table {
  width: 100%;
  table-layout: auto;
  margin-top: 20px;
}
table thead {
  border-bottom: 2px solid #DDD;
}
table thead td {
  font-weight: 500;
  min-height: 40px;
  cursor: default;
  text-align: center;
  vertical-align: middle;
}
table thead td .button {
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
}
table tbody td {
  text-align: center;
  width: 40px;
  cursor: pointer;
  user-select: none;
  vertical-align: middle;
}
table tbody td:not(.disable):hover {
  background-color: #EEE;
}
table tbody td .button {
  cursor: pointer;
}
table tbody td span {
  display: inline-block;
  padding: 5px;
  min-width: 37px;
}
table tfoot {
  border-top: 2px solid #DDD;
}
table tfoot [colspan] {
  text-align: right;
}
table td:last-child {
  text-align: center;
}
table td:last-child .is-payed,
table td:last-child .un-payed,
table td:last-child .total {
  cursor: default;
  display: inline-block;
  width: 40px;
  padding: 5px 10px;
}
table .is-payed {
  background-color: #006600;
  color: white;
}
table .is-payed:not(.disable):hover {
  background-color: #282;
  color: #ccc;
}
table .un-payed {
  background-color: #8B0000;
  color: white;
}
table .un-payed:not(.disable):hover {
  background-color: #AD2222;
}
table .total {
  background-color: #CCCCCC;
}
table .total:not(.disable):hover {
  background-color: #DDD;
}
.modal {
  position: fixed;
  z-index: 1;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.3);
}
.modal.hidden {
  display: none;
}
.modal.shown {
  display: flex;
}
.modal .modal-dialog {
  position: relative;
  width: 700px;
  height: auto;
  background-color: white;
  z-index: 11;
}
.modal .modal-dialog .modal-header {
  display: flex;
  justify-content: space-between;
}
.modal .modal-dialog .modal-header span {
  font-size: 27px;
}
