.react-flow__node-message {
  border-left: 3px solid #009900;
  border-top: 1px solid var(--primary-color);
  border-right: 1px solid var(--primary-color);
  border-bottom: 1px solid var(--primary-color);
  width: 300px;
  height: auto !important;
  padding: 8px;
  background-color: white;
  border-radius: 5px;
}
.react-flow__node-message.selected {
  border-left: 3px solid var(--primary-color-dark);
  border-top: 1px solid var(--primary-color-dark);
  border-right: 1px solid var(--primary-color-dark);
  border-bottom: 1px solid var(--primary-color-dark);
}
.react-flow__node-message.selected .react-flow__handle-left {
  border: 1px solid var(--primary-color-dark);
}
.react-flow__node-message.selected .react-flow__handle-right {
  border: 1px solid var(--primary-color-dark);
}
.react-flow__node-message .react-flow__handle-left {
  width: 10px;
  height: 10px;
  background-color: white;
  border: 1px solid var(--primary-color);
  left: -1.5px;
  top: 25px;
}
.react-flow__node-message .react-flow__handle-right {
  width: 10px;
  height: 10px;
  background-color: white;
  border: 1px solid var(--primary-color);
  right: -1px;
  top: calc(100% - 25px);
}
.react-flow__node-message .header {
  font-size: 21px;
  font-weight: 500;
  text-transform: capitalize;
}
.react-flow__node-message .content {
  background-color: deepskyblue;
  padding: 5px;
  border-radius: 5px;
}
.react-flow__node-message.selected .controls {
  position: absolute;
  right: 0;
  bottom: -15px;
}
