.react-flow__node {
  border-top: 1px solid var(--primary-color);
  border-right: 1px solid var(--primary-color);
  border-bottom: 1px solid var(--primary-color);
  width: 300px;
  height: auto !important;
  padding: 8px;
  background-color: white;
  border-radius: 5px;
}
.react-flow__node.selected {
  border-left: 3px solid var(--primary-color-dark);
  border-top: 1px solid var(--primary-color-dark);
  border-right: 1px solid var(--primary-color-dark);
  border-bottom: 1px solid var(--primary-color-dark);
}
.react-flow__node.selected .react-flow__handle-left {
  border: 1px solid var(--primary-color-dark);
}
.react-flow__node.selected .react-flow__handle-right {
  border: 1px solid var(--primary-color-dark);
}
.react-flow__node .react-flow__handle-left {
  width: 10px;
  height: 10px;
  background-color: white;
  border: 1px solid var(--primary-color);
  left: -1.5px;
  top: 25px;
}
.react-flow__node .react-flow__handle-right {
  width: 10px;
  height: 10px;
  background-color: white;
  border: 1px solid var(--primary-color);
  right: -1px;
  top: calc(100% - 25px);
}
