.title {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-transform: capitalize;
  border-bottom: 1px solid var(--primary-color);
  padding: 0 0 10px;
  margin-bottom: 10px;
  height: 42px;

  .controls {
    display: flex;
    justify-content: center;
    align-items: center;
    border: none;
    background-color: transparent;
    border-radius: 16px;
    margin: 5px;
    .action {
      border: none;
      background-color: transparent;
    }
  }

  .header {
    font-size: 24px;
    font-weight: 500;
    text-transform: uppercase;
  }

  &.only-title {
    border: none;
    margin-bottom: 0;
    padding-bottom: 0;
  }
}
