@import "../../App.css";

.wrapper {
  flex-grow: 1;
  height: 100%;
}

.btn-shadow {
  min-width: 50px;
  min-height: 50px;
  background-color: var(--white);
  border: 1px solid var(--primary-color);
  box-shadow: 0 0 10px var(--primary-color);
  outline: none;
  margin: 10px;
  &:hover {
    background-color: var(--white);
    border: 1px solid var(--white);
    box-shadow: 0 0 15px var(--primary-color-dark);
    outline: none;
  }
  &:active, &:focus, &:focus-visible, &:focus-within {
    border: 1px solid var(--white);
    outline: none;
  }
}