.advanced-panel {
  position: fixed;
  top: 0;
  right: 0;
  height: 100vh;
  width: 500px;
  background-color: white;
  border-left: 1px solid #cccccc;
  box-shadow: 0 0 10px #ccc;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  &.show {
    display: flex;
  }
  &.hidden {
    display: none;
  }
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    height: 82px;
    border-bottom: 1px solid var(--primary-color);
    padding: 20px;
  }
  .content {
    height: calc(100vh - (82px + 42px));
    padding: 20px;
    .form-input {
      margin-bottom: 10px;
    }
  }
  .footer {
    height: 42px;
    padding: 10px 20px;
    display: flex;
    align-items: center;
    border-top: 1px solid var(--primary-color);
  }
}