.calendar-container {
    width: 100%;
    max-width: 800px;
    margin: 0 auto;
    padding: 20px;
    margin: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
    border-radius: 8px;
    background-color: #fff;
}

.calendar-header {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 20px;
}

.calendar-header button {
    padding: 8px 16px;
    border: none;
    background-color: #f0f0f0;
    border-radius: 4px;
    cursor: pointer;
    font-size: 16px;
}

.calendar-header button:hover {
    background-color: #e0e0e0;
}

.calendar-header h2 {
    margin: 0;
    font-size: 24px;
}

.calendar-grid {
    display: grid;
    grid-template-columns: repeat(7, 1fr);
    gap: 4px;
}

.weekday {
    text-align: center;
    font-weight: bold;
    padding: 10px;
    background-color: #f5f5f5;
}

.calendar-day {
    padding: 15px;
    text-align: center;
    cursor: pointer;
    border-radius: 4px;
    transition: background-color 0.2s;
}

.calendar-day:hover {
    background-color: #f0f0f0;
}

.calendar-day.different-month {
    color: #ccc;
}

.calendar-day.selected {
    background-color: #007bff;
    color: white;
}

.calendar-day.selected:hover {
    background-color: #0056b3;
}
