table {
	width: 100%;
	table-layout: auto;
	margin-top: 20px;

	thead {
		border-bottom: 2px solid #DDD;

		td {
			font-weight: 500;
			min-height: 40px;
			cursor: default;
			text-align: center;
			vertical-align: middle;
			.button {
				display: flex;
				justify-content: center;
				align-items: center;
				cursor: pointer;
			}
		}
	}

	tbody {
		td {
			text-align: center;
			width: 40px;
			cursor: pointer;
			user-select: none;
			vertical-align: middle;

			&:not(.disable):hover {
				background-color: #EEE;
			}

			.button {
				cursor: pointer;
			}

			span {
				display: inline-block;
				padding: 5px;
				min-width: 37px;
			}
		}
	}

	tfoot {
		border-top: 2px solid #DDD;

		[colspan] {
			text-align: right;
		}
	}

	td:last-child {
		text-align: center;

		.is-payed, .un-payed, .total {
			cursor: default;
			display: inline-block;
			width: 40px;
			padding: 5px 10px;
		}
	}

	.is-payed {
		background-color: #006600;
		color: white;

		&:not(.disable):hover {
			background-color: #282;
			color: #ccc;
		}

	}

	.un-payed {
		background-color: #8B0000;
		color: white;

		&:not(.disable):hover {
			background-color: #AD2222;
		}

	}

	.total {
		background-color: #CCCCCC;

		&:not(.disable):hover {
			background-color: #DDD;
		}
	}
}

.modal {
	position: fixed;
	z-index: 1;
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,.3);
	&.hidden {
		display: none;
	}

	&.shown {
		display: flex;
	}

	.modal-dialog {
        position: relative;
		width: 700px;
		height: auto;
		background-color: white;
		z-index: 11;

		.modal-header {
			display: flex;
			justify-content: space-between;

			span {
				font-size: 27px;
			}
		}
	}
}